export class DeclarationAttribution {
    attributaire: string
    avisAttributionPath: string
    intitulePublication: string
    dateParution: Date

    constructor(
        attributaire: string,
        avisAttributionPath: string,
        intitulePublication: string,
        dateParution: Date
    ) {
        this.attributaire = attributaire,
            this.avisAttributionPath = avisAttributionPath,
            this.intitulePublication = intitulePublication,
            this.dateParution = dateParution
    }
    
    static getDummy(): DeclarationAttribution {
        return new DeclarationAttribution(
            "attributaire",
            "avisAttributionPath",
            "intitulePublication",
            new Date()
        )
    }
}